.header {
  display: flex;
}

.header-content {
  flex: 1;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  margin-right: 5rem;
}

.header-content h1 {
  font-family: var(--font-family);
  font-weight: 800;
  font-size: 82px;
  line-height: 95px;
  letter-spacing: -0.04em;
}

.header-content p {
  font-family: var(--font-family);
  font-size: 20px;
  line-height: 27px;
  color: var(--color-text);
  margin-top: 1.5rem;
}

.header-image {
  flex: 1;
  display: flex;
  justify-content: center;
  align-items: center;
}
.header-image img {
  width: 100%;
}

@media screen and (max-width: 1050px) {
  .header {
    flex-direction: column;
  }
  .header-content {
    margin: 0 0 3rem;
  }
}

@media screen and (max-width: 650px) {
  .header h1 {
    font-size: 48px;
    line-height: 60px;
  }
  .header p {
    font-size: 16px;
    line-height: 24px;
  }
}

@media screen and (max-width: 490px) {
    .header h1 {
      font-size: 36px;
      line-height: 48px;
    }
    .header p {
      font-size: 14px;
      line-height: 24px;
    }
  }